import './NavBar.css';
import {
	BrowserRouter as Router,
	Switch,
	Route,
	Link,
	NavLink,
	useLocation
  } from "react-router-dom";
import { ReactComponent as Logo } from '../img/logo.svg';
import { ReactComponent as InstagramIcon } from '../img/insta.svg';
import IconButton from '@mui/material/IconButton';

function NavBar() {
	return (
		<nav className="NavBar-container">
			<div className="NavBar-leading">
				<Logo className="NavBar-logo" />
			</div>
			<div className="NavBar-trailing">
				<NavBarLink link="home">Accueil</NavBarLink>
				<NavBarLink link="pint-holder">Porte-Pinte Foy</NavBarLink>
				<IconButton className="NavBar-icon" href="https://www.instagram.com/labinsarennes/" target="_blank" >
					<InstagramIcon/>
				</IconButton>
			</div>
		</nav>);
}

function NavBarLink(props) {
	return (
		<Link to={"/" + props.link} className={"NavBar-link" + (useLocation().pathname == ("/" + props.link) ? " NavBar-link-activated" : "" )}>
			{props.children}
		</Link>
	);
}
	
export default NavBar;