
import { Input, TextField } from "@mui/material";
import { styled } from "@mui/system";
import './LabComponents.css';

function SplitSection(props) {
    return (
        <section className="LabComponents-split-section" {...props}>
        {props.children}
        </section>
    );
}
  
function SplitSectionContainer(props){
    return (
        <div className="LabComponents-split-section-container" {...props}>
        {props.children}
        </div>
    );
}
  
function MainSection(props) {
    return (
      <section className="LabComponents-main-section" {...props}>
        {props.children}
      </section>
    );
}

function RoundedTextField(props){
    return (
        <input {...props} className="RoundedTextField-input"></input>
    );
}

export {SplitSection, SplitSectionContainer, MainSection, RoundedTextField};