import {SplitSectionContainer, SplitSection, MainSection, RoundedTextField} from '../components/LabComponents'
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import { Switch , MenuItem, Select} from '@mui/material';
import './PintHolder.css';
import React from 'react';
import { CircularProgress, Snackbar } from '@mui/material';
import pintRender from '../img/render.png'
import QRCode from 'react-qr-code';

const STEPS = {
    name: 'name',
    link: 'link',
    payment: 'payment'
}

const NOTIFICATIONS = {
    none: 'none',
    no_name: 'no_name'
}

class PintHolder extends React.Component {

    constructor(props){
        super(props);
        this.state = {
            userName: "",
            customLink: "",
            email: "",
            isInstagramLink: true,
            isInstagramAccountValid: false,
            step: STEPS.name,

            isDebouncing: false,

            notificationOpen: false,
            notificationMessage: NOTIFICATIONS.none
        };
        this.handleNameChange = this.handleNameChange.bind(this);
        this.handleStartOrder = this.handleStartOrder.bind(this);
        this.handleLinkChange = this.handleLinkChange.bind(this);
        this.handleIsInstagramLinkChange = this.handleIsInstagramLinkChange.bind(this);
        this.handleGoToPayment = this.handleGoToPayment.bind(this);
        this.handleEmailChange = this.handleEmailChange.bind(this);
        this.getPage = this.getPage.bind(this);
    }

    handleNameChange(event) {
        this.setState({userName: event.target.value, notificationOpen: false});
    }

    handleStartOrder(event) {
        
        if(this.state.userName == ""){
            this.setState({
                notificationOpen: true,
                notificationMessage: NOTIFICATIONS.no_name
            });
        }else{
            this.setState({
                step: STEPS.link
            });
        }
    }

    handleLinkChange(event){
        this.setState({customLink: event.target.value});
    }

    handleIsInstagramLinkChange(event) {
        this.setState({isInstagramLink: !event.target.checked});
    }

    handleGoToPayment() {
        this.setState({step: STEPS.payment});
    }

    handleEmailChange(event) {
        this.setState({email: event.target.value});
    }

    getNotificationMessage(){
        switch (this.state.notificationMessage) {
            case NOTIFICATIONS.no_name:
                return "N'oublie pas de rentrer ton nom avant de continuer";
        
            default:
                return "";
        }
    }    

    getPage(){
        switch (this.state.step) {
            case STEPS.name:
            return <div>
                <SplitSectionContainer style={{marginTop: '3em'}}>
                    <SplitSection style={{textAlign: 'end', alignItems: 'end', marginBottom: '8em'}} >
                        <h1>Le Porte-Pinte à ton nom</h1>
                        <p>Réserve ton porte-pinte à ton prope nom et deviens swaggax lors de tes aventures au Foy, pour seulement 2€</p>
                        <RoundedTextField
                        placeholder="Prénom"
                        style={{
                            marginTop: '1em'
                        }}
                        onChange={this.handleNameChange}
                        />
                    </SplitSection>
                    <SplitSection style={{alignItems: 'start'}} >
                        <img src={pintRender} alt="Porte Pinte, Rendu 3D" className="PintHolder-render" />
                    </SplitSection>
                </SplitSectionContainer>
                <MainSection>
                    <Button className="App-button" variant="contained" color="primary" disableElevation={true} onClick={this.handleStartOrder}>
                    Je commande
                    </Button>
                </MainSection>
                <Snackbar
                open={this.state.notificationOpen}
                message={this.getNotificationMessage()}
                />
            </div>;
        
            case STEPS.link:
                return (
                <div>
                    <SplitSectionContainer style={{marginTop: '5em'}}>
                        <SplitSection style={{textAlign: 'end', alignItems: 'end'}} >
                            <h1>Personalise-le avec ton Insta</h1>
                            <p>Indique ton compte Insta et profite d'un QR code personalisé directement sur ton porte-pinte. Si tu veux, tu peux aussi mettre n'importe quel lien internet!</p>
                            
                            <div >
                                
                            <span className="PintHolder-at">{this.state.isInstagramLink ? "@" : "http://"}</span>
                                <RoundedTextField
                                placeholder={this.state.isInstagramLink ? "Ton compte Insta" : "exemple.fr"}
                                
                                style={{
                                    marginTop: '1em',
                                }}
                                id="other"
                                onChange={this.handleLinkChange}
                                />
                            </div>
                            <div style={{marginTop: '1em'}} >
                                <Switch checked={!this.state.isInstagramLink} onChange={this.handleIsInstagramLinkChange} defaultChecked size="small" />
                                <label>Url personalisé</label>
                            </div>
                        </SplitSection>
                        <SplitSection>
                            {this.qrCode()}
                        </SplitSection>
                    </SplitSectionContainer>
                    <MainSection>
                        <Button className="App-button" variant="contained" color="primary" disableElevation={true} onClick={this.handleGoToPayment}>
                        Continuer
                        </Button>
                    </MainSection>
                    <Snackbar
                    open={this.state.notificationOpen}
                    message={this.getNotificationMessage()}
                    />
                </div>
                    
                );

            case STEPS.payment:
                return (
                    <MainSection style={{marginTop: '5em'}}>
                        <h1>Validation et payement</h1>
                        <p>Donne-nous ton mail pour qu'on puisse te contacter quand ton porte-pinte sera prêt. Quand tu viendras le retirer, prépare de quoi payer les 2€</p>
                        
                        <RoundedTextField
                            placeholder="Ton email"
                            
                            style={{
                                marginTop: '1em',
                                marginBottom: '3em'
                            }}
                            onChange={this.handleLinkChange}
                        />

                        <Button className="App-button" variant="contained" color="primary" disableElevation={true} onClick={this.handleGoToPayment}>
                        Valider
                        </Button>
                    </MainSection>
                );

            default:
                return (<div></div>);
        }
    }

    qrCode() {
        return this.state.isDebouncing  ? <CircularProgress/> : <QRCode value={this.state.isInstagramLink ? ( "https://instagram.com/" + this.state.customLink) : "http://" + this.state.customLink } />;
    }

    render() {
        return this.getPage();
    } 
}

export default PintHolder;